import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import { FacturaspagadasService } from '../facturaspagadas/facturaspagadas.service';
import { ToastService } from '../toast.service';
import { map } from 'rxjs/operators';
import { LoadingController } from '@ionic/angular';

@Component({
  selector: 'app-cartola',
  templateUrl: './cartola.page.html',
  styleUrls: ['./cartola.page.scss']
})
export class CartolaPage implements OnInit {
  @ViewChild('mydatatable') mydatatable: DatatableComponent;
  selectCartola = 1;
  fecHoy = Date.now();
  perActual =
    new Date(this.fecHoy).getFullYear() +
    '-' +
    (new Date(this.fecHoy).getMonth()).toLocaleString();
  periodoActual;
  fecInicial;
  fecFinal;
  Usuario: string;
  textoTabla = {
    emptyMessage: 'Cargando.... (Puede tardar un poco...)',
    totalMessage: 'Documentos'
  };
  dataTable;
  submit: boolean;

  constructor(private dataService: FacturaspagadasService, private toastService: ToastService, private loadingCtrl: LoadingController) { }

  private verificacionMes(mes: string) {
    let mess = String(Number(mes) - 1);
    if (mess.length === 1) {
      mess = '0' + mess;
    }
    return mess;
  }

  ngOnInit() {
    this.Usuario = localStorage.getItem('rut');
    // tslint:disable-next-line: max-line-length
    this.periodoActual =
      new Date(this.fecHoy).getFullYear() +
      '-' +
      this.verificacionMes(
        (new Date(this.fecHoy).getMonth() + 1).toLocaleString()
      );
    this.fecInicial = this.periodoActual;
  }

  segmentChanged(ev: any) {
    this.selectCartola = Number(ev.detail.value);
    if (this.selectCartola === 3) {
      this.dataService.getPeriodos()
        .pipe(
          map((res: any) => {
            res.forEach(element => {
              let ano = Number(element.periodo.split('-')['0']);
              let mes = Number(element.periodo.split('-')['1']) + 1;
              let str_mes = '';
              if (mes === 13) {
                mes = 1;
                ano = ano + 1;
              }
              if (mes < 10) {
                str_mes = '0' + mes;
              } else {
                str_mes = mes.toString();
              }
              const per = ano + '-' + str_mes;
              element.per = per;
            });
            return res;
          })
        )
        .subscribe(data => {
          console.log(data);
          this.dataTable = data;
          console.log(this.dataTable);
        });
    }
    // console.log(this.mydatatable);
  }

  verificarFecha(e) {
    if (this.fecInicial > this.fecFinal) {
      this.fecFinal = this.fecInicial;
    }
  }

  downloadCartolaMes() {
    this.submit = true;
    this.dataService.presentLoading('Generando Cartola...');
    this.dataService.getCartola().subscribe(response => {
      this.downLoadFile(response, 'application/pdf', this.perActual);
      this.dataService.dismissLoading();
    }, error => {
      this.dataService.dismissLoading();
      this.submit = false;
      this.toastService.presentToast('Error de comunicación con el servidor');
    });
  }

  downloadCartolaHistorica(periodo) {
    this.submit = true;
    this.dataService.presentLoading('Generando Cartola...');
    this.dataService.getCartolaHistorica(periodo).subscribe(response => {
      this.downLoadFile(response, 'application/pdf', periodo);
      this.dataService.dismissLoading();
    }, error => {
      this.dataService.dismissLoading();
      this.submit = false;
      this.toastService.presentToast('Error de comunicación con el servidor');
    });
  }


  downLoadFile(data: any, type: string, periodo: string) {
    const a = document.createElement('a');
    document.body.appendChild(a);
    a.style.display = 'none';

    const blob = new Blob([data], { type });
    const url = window.URL.createObjectURL(blob);
    a.href = url;
    a.download = 'Cartola_' + periodo + '.pdf';
    a.click();
    window.URL.revokeObjectURL(url);
    this.submit = false;
  }
}
